.menu__icon {
  display: none;
}
@media screen and (max-width: 1024px) {
  .menu__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.2);
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    width: 44px;
    height: 44px;
    position: relative;
    z-index: 5;
  }
  .menu__icon::before {
    content: "";
    position: absolute;
    width: 20px;
    height: 3px;
    background: #FFFFFF;
    border-radius: 8px;
    top: 11px;
    left: 50%;
    transform: translate(-50%, 0);
    opacity: 0;
    transition: 0.3s ease;
  }
  .menu__icon._active::before {
    opacity: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(-45deg);
  }
  .menu__icon::after {
    content: "";
    position: absolute;
    width: 20px;
    height: 3px;
    background: #FFFFFF;
    border-radius: 8px;
    bottom: 11px;
    left: 50%;
    transform: translate(-50%, 0);
    opacity: 0;
    transition: 0.3s ease;
  }
  .menu__icon._active::after {
    opacity: 1;
    bottom: 50%;
    left: 50%;
    transform: translate(-50%, 50%) rotate(45deg);
  }
  .menu__icon._active img {
    opacity: 0;
  }
}

.header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  padding: 17px 0;
}
.header__row {
  display: flex;
  align-items: center;
}
.header__logo {
  flex: 1 1 auto;
}
.header__menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 1024px) {
  .header__menu {
    padding: 95px 15px 0;
    flex-direction: column-reverse;
    align-items: stretch;
    justify-content: flex-end;
    position: fixed;
    top: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(189.48deg, #20030B -42.96%, #480F1F 92.84%);
    overflow: auto;
    left: 0;
    z-index: 4;
    transition: top 0.3s ease 0s;
  }
  .header__menu._active {
    top: 0;
  }
}
.header__nav {
  flex: 1 1 auto;
}
@media screen and (max-width: 1024px) {
  .header__nav {
    flex: 0 1 auto;
    max-width: 500px;
    margin: 0 auto;
    width: 100%;
  }
}
.header__search {
  position: relative;
  display: flex;
  align-items: center;
}
.header__search-input {
  position: absolute;
  font-family: "Popins-reg", sans-serif;
  display: inline-block;
  width: 350px;
  padding: 10px 10px 10px 30px;
  font-size: 17px;
  line-height: 30px;
  border-radius: 12px;
  background: #FFFFFF;
  border: 1.5px solid #808080;
  color: #808080;
  right: -6px;
  top: 50px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
.header__search-input._active {
  width: 350px;
  opacity: 1;
  visibility: visible;
}
.header__search-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: #FFFFFF;
  border-radius: 12px;
  cursor: pointer;
  position: relative;
  z-index: 2;
  transition: background-color 0.3s ease;
}
.header__search-btn::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  width: 25px;
  height: 3px;
  border-radius: 5px;
  background-color: #E1712C;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.header__search-btn::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  width: 25px;
  border-radius: 5px;
  height: 3px;
  background-color: #E1712C;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.header__search-btn._active::before, .header__search-btn._active::after {
  opacity: 1;
}
.header__search-btn:hover {
  background: #c5c3c3;
}
.header__search-image {
  fill: #E1712C;
  transition: opacity 0.3s ease;
  opacity: 1;
}
.header__search-image._active {
  opacity: 0;
}

@media screen and (max-width: 1024px) {
  .header__search {
    margin: 0 auto 10px;
    max-width: 500px;
    width: 100%;
  }
  .header__search-input {
    border: none;
    display: block;
    opacity: 1;
    visibility: visible;
    position: static;
    width: 100%;
    padding: 16px 13px 14px 45px;
    font-size: 17px;
    line-height: 25px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 12px;
  }
  .header__search-input::placeholder {
    color: #FFFFFF;
  }
  .header__search-btn {
    background: transparent;
    position: absolute;
    left: 5px;
    top: 50%;
    transform: translateY(-50%);
  }
  .header__search-btn:hover {
    background: transparent;
  }
  .header__nav {
    overflow-y: scroll;
    padding: 60px 0 60px 0;
  }
}
.menu__list {
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu__item {
  position: relative;
}
.menu__item:not(:last-child) {
