}
.menu__link {
  cursor: pointer;
  position: relative;
  z-index: 2;
  font-family: "Popins-reg", sans-serif;
  font-size: 18px;
  color: #FFFFFF;
  transition: color 0.3s ease;
}
.menu__link:hover {
  color: #c5c3c3;
}
@media screen and (max-width: 1024px) {
  .menu__link {
    font-size: 22px;
    line-height: 24px;
  }
}

@media screen and (max-width: 1024px) {
  .menu__list {
    flex-direction: column;
    align-items: flex-start;
  }
  .menu__item {
    width: 100%;
  }
  .menu__item:not(:last-child) {
    margin: 0 0 25px 0;
  }
  .menu__item-sub_list {
    flex-direction: column;
    align-items: flex-start;
    position: relative;
  }
  .menu__item-sub_list._open span::after {
    transform: rotate(180deg);
  }
  .menu__item-sub_list:hover::before {
    content: none;
  }
  .menu__item-sub_list:hover .menu__link {
    color: #FFFFFF;
  }
  .menu__item-list {
    width: 100%;
    position: static;
    display: none;
    background: transparent;
    box-shadow: none;
    padding: 20px 0;
    border-top: 1px solid rgba(255, 255, 255, 0.14);
    border-bottom: 1px solid rgba(255, 255, 255, 0.14);
    border-radius: 0;
    margin-top: 20px;
  }
  .menu__item-list._open {
    display: block;
  }
  .menu__item-item:not(:last-child) {
    margin: 0 0 20px 0;
  }
