  margin: 0 0 20px 0;
}
.news__blog ol li p {
  margin: 0;
}
.news__blog ul {
  margin: 0 0 30px 0;
}
.news__blog ul li {
  position: relative;
  padding: 0 0 0 47px;
}
.news__blog ul li::before {
  content: "i";
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 5px;
  left: 0;
  width: 34px;
  height: 34px;
  font-size: 25px;
  line-height: 30px;
  color: #FFFFFF;
  font-family: sans-serif;
  border-radius: 8px;
  background: linear-gradient(252.32deg, #8A2336 0%, #631728 28.99%, #340918 100%);
}
.news__blog ul li:not(:last-child) {
  margin: 0 0 15px 0;
}
.news__blog ul li p {
  margin: 0;
}
.news__blog blockquote {
  position: relative;
  padding: 85px 30px 20px;
  border-radius: 8px;
  background: linear-gradient(252.32deg, #8A2336 0%, #631728 28.99%, #340918 100%);
  color: #FFFFFF;
  font-size: 15px;
  line-height: 30px;
  letter-spacing: 0.01em;
  margin: 0 0 20px 0;
}
.news__blog blockquote::before {
  pointer-events: none;
  content: "‟";
  font-size: 65px;
  line-height: 65px;
  transform: scale(1, -1);
  height: 18px;
  color: #E1712C;
  position: absolute;
  display: inline-block;
  top: 30px;
  left: 30px;
}
.news__blog blockquote::after {
  pointer-events: none;
  content: "‟";
  font-size: 65px;
  line-height: 65px;
  transform: scale(-1, 1);
  height: 18px;
  color: #E1712C;
  position: absolute;
  display: inline-block;
  top: 40px;
  left: 52px;
}

@media screen and (max-width: 1024px) {
  .news__row {
    flex-direction: column;
  }
  .news__blog {
    margin: 0 0 20px 0;
    max-width: 100%;
  }
}
@media screen and (max-width: 600px) {
  .news {
    padding: 50px 0;
  }
  .news__container {
    padding: 0;
  }
  .news__blog {
    padding: 0 15px;
  }
  .news__blog h2 {
    font-size: 20px;
    line-height: 30px;
    margin: 0 0 15px 0;
  }
  .news__blog h3, .news__blog h4, .news__blog h5, .news__blog h6 {
    font-size: 17px;
    line-height: 26px;
  }
  .news__blog p, .news__blog li {
    font-size: 13px;
    line-height: 22px;
    margin: 0 0 15px 0;
  }
  .news__blog ol {
    margin: 0 0 20px 0;
  }
  .news__blog ol li:not(:last-child) {
    margin: 0 0 10px 0;
  }
  .news__blog ul {
    margin: 0 0 20px 0;
  }
  .news__blog blockquote {
    padding: 85px 20px 20px 20px;
    font-size: 13px;
    line-height: 28px;
  }
}